import React, { useState, useEffect } from "react"
import { Headset, HouseFill, ThreeDots } from "react-bootstrap-icons"
import { Link } from "gatsby"
import HeaderContentLayerDefault from "globalOrganisms/Sidebar/content-layers/header/default"
import HeaderContentLayerMenu from "globalOrganisms/Sidebar/content-layers/header/menu"
import MainContentLayerDefault from "globalOrganisms/Sidebar/content-layers/main/default"
import MainContentLayerMenu from "globalOrganisms/Sidebar/content-layers/main/menu"
import FooterContentLayerDefault from "globalOrganisms/Sidebar/content-layers/footer/default"
import * as S from "globalOrganisms/Sidebar/styles"

export default function Sidebar() {
  const [headerContentLayer, setHeaderContentLayer] = useState("default")
  const [mainContentLayer, setMainContentLayer] = useState("default")
  const [footerContentLayer, setFooterContentLayer] = useState("default")

  const styleProps = {
    maxWidth: "360px",
  }

  let sidebar,
    sidebarClassList,
    sidebarHeaderHeight,
    sidebarFooterHeight,
    sidebarHeaderCompensator,
    sidebarFooterCompensator

  useEffect(() => {
    sidebar = document.querySelector(".sidebar-content")
    sidebarClassList = sidebar.classList

    sidebarHeaderHeight = document.querySelector(".sidebar-header").clientHeight
    sidebarHeaderCompensator = document.querySelector(
      ".sidebar-header-compensator"
    )
    sidebarHeaderCompensator.style.height = `${sidebarHeaderHeight}px`

    sidebarFooterHeight = document.querySelector(".sidebar-footer").clientHeight
    sidebarFooterCompensator = document.querySelector(
      ".sidebar-footer-compensator"
    )
    sidebarFooterCompensator.style.height = `${sidebarFooterHeight * 3}px`
  })

  function getHeaderContentLayer(headerContentLayer) {
    switch (headerContentLayer) {
      case "menu":
        return <HeaderContentLayerMenu />
      default:
        return <HeaderContentLayerDefault />
    }
  }

  function getMainContentLayer(mainContentLayer) {
    switch (mainContentLayer) {
      case "menu":
        return <MainContentLayerMenu />
      default:
        return <MainContentLayerDefault />
    }
  }

  function getFooterContentLayer(footerContentLayer) {
    switch (footerContentLayer) {
      // case "default":
      //   return
      default:
        // return <FooterContentLayerDefault />
        return (
          <>
            <button
              className={"element-button footer-button"}
              onClick={event => {
                const neededContentLayer = "default"
                sidebarVisibilityHandler(neededContentLayer)
              }}
            >
              <i>
                <HouseFill size={18} />
              </i>
              <span className={"caption__large"}>Каталог</span>
            </button>
            <Link
              to={"/contacts"}
              className={"element-button footer-button"}
              // onClick={event => {
              //   //const neededContentLayer = "menu"
              //   sidebarVisibilityHandler(neededContentLayer)
              // }}
            >
              <i>
                <Headset size={18} />
              </i>
              <span className={"caption__large"}>Помощь</span>
            </Link>
            <button
              className={"element-button footer-button"}
              onClick={event => {
                const neededContentLayer = "menu"
                sidebarVisibilityHandler(neededContentLayer)
              }}
            >
              <i>
                <ThreeDots size={18} />
              </i>
              <span className={"caption__large"}>Меню</span>
            </button>
          </>
        )
    }
  }

  function sidebarVisibilityHandler(neededContentLayer) {
    if (
      mainContentLayer === neededContentLayer ||
      sidebarClassList.toString().indexOf("visible") === -1
    ) {
      sidebar.classList.toggle("visible")
    }
    if (mainContentLayer !== neededContentLayer) {
      setHeaderContentLayer(neededContentLayer)
      setMainContentLayer(neededContentLayer)
      setFooterContentLayer(neededContentLayer)
    }
    return
  }

  return (
    <>
      <S.SideBar styleProps={styleProps}>
        <header className={"bg-background_primary sidebar-header"}>
          {getHeaderContentLayer(headerContentLayer)}
        </header>
        <div className={"sidebar-content"}>
          <div className={"sidebar-header-compensator"}></div>
          {getMainContentLayer(mainContentLayer)}
          <div className={"sidebar-footer-compensator"}></div>
        </div>
        <footer className={"bg-background_primary sidebar-footer"}>
          {getFooterContentLayer(footerContentLayer)}
        </footer>
      </S.SideBar>
    </>
  )
}
