import styled from "styled-components"

export const Main = styled.main`
  display: flex;
  justify-content: center;
  & .main-content {
    position: relative;
    padding: 80px 0 0 0;
    width: 100%;
    max-width: 1200px;
    @media (max-width: 1400px) {
      margin-left: 0;
      padding: 80px 32px 0 32px;
    }
    @media (max-width: 576px) {
      padding: 80px 0 0 0;
    }
  }
`
