import projectConfig from "projectConfig"
import { createGlobalStyle } from "styled-components"
const Color = require(`color`)

const { styleVariables: siteStyleVariables } = projectConfig
const { root } = siteStyleVariables

export const GlobalStyles = createGlobalStyle`
  html, #___gatsby, #gatsby-focus-wrapper {
    height: 100%
  }
  body {
    font-family: ${root.defaultFontFamily};
    font-size: 17px;
    line-height: 22px;
    background-color: ${({ theme }) => theme.background_primary};
    color: ${({ theme }) => theme.text_primary};
    -webkit-font-smoothing: antialiased
  }
  a {
    color: ${({ theme }) => theme.tint_blue};
    &:hover {
      color: ${({ theme }) => theme.tint_blue};
    }
    &:focus-visible {
      outline: none;
    }
  }
  article {
    h1 {
      margin-bottom: 32px;
    }
    ol ,ul {
      margin-bottom: 24px;
    }
    h2, h3, h4, h5, h6, p {
      margin-bottom: 16px;
    } 
    ol, ul {
      li {
        margin-bottom: 8px;
        color: ${({ theme }) => theme.text_tertiary};
      }
    }
    p {
      color: ${({ theme }) => theme.text_tertiary};
    }
  }
  .large-title__large {
    font-size: 34px;
    line-height: 41px;
    font-weight: normal;
  }
  h1, .h1__large {
    font-size: 28px;
    line-height: 34px;
    font-weight: normal;
  }
  h2, .h2__large {
    font-size: 22px;
    line-height: 28px;
    font-weight: normal;
  }
  h3, .h3__large {
    font-size: 20px;
    line-height: 24px;
    font-weight: normal;
  }
  .body__large {
    font-size: 17px;
    line-height: 22px;
    font-weight: normal;
  }
  .callout__large {
    font-size: 16px;
    line-height: 21px;
    font-weight: normal;
  }
  .subhead__large {
    font-size: 15px;
    line-height: 20px;
    font-weight: normal;
  }
  .caption__large {
    font-size: 12px;
    line-height: 16px;
    font-weight: normal;
  }
  .text_primary {
    color: ${({ theme }) => theme.text_primary};
  }
  .text_secondary {
    color: ${({ theme }) => theme.text_secondary};
  }
  .text_tertiary {
    color: ${({ theme }) => theme.text_tertiary};
  }
  .color-gray {
    color: ${({ theme }) => theme.gray};
  }
  .color-blue {
   color: ${({ theme }) => theme.tint_blue} !important;
  }
  .element-large {
    min-width:48px;
    min-height: 48px;
    font-size: 17px;
    line-height: 24px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      & div {
        width: 36px;
        height: 36px;
      }
    }
  }
  .element-medium {
    min-width:40px;
    min-height: 40px;
    font-size: 15px;
    line-height: 24px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      & div {
        width: 36px;
        height: 36px;
      }
    }
  }
  .element-small {
    min-width:32px;
    min-height: 32px;
    font-size: 12px;
    line-height: 16px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      & div {
        width: 32px;
        height: 32px;
      }
    }
  }
  button.element-large {
    grid-gap: 12px;
    padding: 12px 16px;
    font-size: 17px;
    line-height: 24px;
  }
  button.element-medium {
    grid-gap: 12px;
    padding: 8px 16px;
    font-size: 15px;
    line-height: 24px;
  }
  button.element-small {
    grid-gap: 8px;
    padding: 8px 12px;
    font-size: 12px;
    line-height: 16px;
  }
  .element-button {
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: none;
    padding: 0;
    font-size: 1rem;
    border-radius: ${root.primaryBorderRadius};
    &.blue {
      color: ${({ theme }) => theme.white};
      background-color: ${({ theme }) => theme.tint_blue};
    }
    &.green {
      color: ${({ theme }) => theme.white};
      background-color: ${({ theme }) => theme.tint_green};
    }
    :focus {
      outline: none;
    }
  }
  .bg-background_primary {
    background-color: ${({ theme }) => theme.background_primary};
  }
  .border-right {
    box-shadow: inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
  }

  /* .border {
    box-shadow: inset 1px 0px 0px ${({ theme }) =>
      theme.separator_non_opaque}, inset -1px 0px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px 1px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
  border: none !important; // temp!
  } */
  .border-top {
    box-shadow: inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque};
    border: none !important; // temp!
  }
  .border-bottom {
    box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
    border: none !important; // temp!
  }
  .border-primary {
    border-radius: ${root.primaryBorderRadius};
  }
  .border-secondary {
    border-radius: ${root.secondaryBorderRadius};
  }
  .border-tertiary {
    border-radius: ${root.tertiaryBorderRadius};
  }
  .adaptive {
    &.bg-primary-to-secondary {
      background-color: ${({ theme }) => theme.background_primary};
      @media (max-width: 1400px) {
        background-color: ${({ theme }) => theme.background_secondary};
  }
    }
    &.text {
      padding: 0;
      @media (max-width: 576px) {
        padding: 0 16px;
  }
    }
    &.border-inset {
      box-shadow: inset 1px 0px 0px ${({ theme }) =>
        theme.separator_non_opaque}, inset -1px 0px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px 1px 0px ${({ theme }) =>
  theme.separator_non_opaque}, inset 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
    border: none !important; // temp!
    }
    &.border-outset {
    box-shadow: 1px 0px 0px ${({ theme }) =>
      theme.separator_non_opaque}, -1px 0px 0px ${({ theme }) =>
  theme.separator_non_opaque}, 0px 1px 0px ${({ theme }) =>
  theme.separator_non_opaque}, 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
    border: none !important; // temp!
  }
    &.rounded {
      border-radius: ${root.secondaryBorderRadius} !important;
      @media (max-width: 576px) {
        border-radius: 0 !important;
  }
    }
    &.rounded-bottom {
    border-radius: 0 0 ${root.secondaryBorderRadius} ${
  root.secondaryBorderRadius
}
  }

  }
  .border-y-inset {
      box-shadow: inset 0px 1px 0px ${({ theme }) =>
        theme.separator_non_opaque}, inset 0px -1px 0px ${({ theme }) =>
  theme.separator_non_opaque};
    border: none !important; // temp!
    }
  :focus {
    outline: none;
  }
  input[type="search"]::-ms-clear {
    display: none;
    width: 0;
    height: 0;
  }
  input[type="search"]::-ms-reveal {
    display: none;
    width: 0;
    height: 0;
  }
  /* clears the 'X' from Chrome */
  input[type="search"]::-webkit-search-decoration,
  input[type="search"]::-webkit-search-cancel-button,
  input[type="search"]::-webkit-search-results-button,
  input[type="search"]::-webkit-search-results-decoration {
    display: none;
  }
  .modal-content {
    border: none;
    border-radius: ${root.secondaryBorderRadius};
    background-color: ${({ theme }) => theme.background_primary};
  }
  .modal-dialog {
    margin: auto;
  }
  .modal-backdrop {
      background-color: rgba(142, 142, 147, 0.4);
      backdrop-filter: blur(1rem);
  }
  .modal-backdrop.show {
      opacity: 1;
  }
  .modal-small {
  max-width: 344px;
  } 
  .carousel-control-prev, .carousel-control-next {
    width: 40px;
  }
  .z-index-1030 {
    z-index: 1030;
  }
`
