import projectConfig from "projectConfig"
import React from "react"
import { Link } from "gatsby"
import Row from "globalAtoms/Row"

export default function HeaderContentLayerDefault() {
  return (
    <Row className={"element-large"}>
      <Link to="/" className={"center-part"}>
        {projectConfig.environment.siteShortTitle}
      </Link>
    </Row>
  )
}
