import styled from "styled-components"

export const MainNav = styled.nav`
  font-size: 17px;
  line-height: 24px;
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 48px;
  position: fixed;
  left: 0;
  z-index: 1030;
  @media (max-width: 1400px) {
    display: none;
  }

  background-color: ${({ theme }) => theme.background_primary};
  box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  .main-nav-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    height: 48px;
    max-width: 1200px;
    .main-nav-site-title {
      display: flex;
      align-items: center;
      margin-right: 48px;
      color: ${({ theme }) => theme.text_secondary};
      opacity: 0.5;
    }
    a {
      display: flex;
      align-items: center;
      height: 100%;
      color: ${({ theme }) => theme.gray_02};
      text-decoration: none;
      margin-right: 24px;
      opacity: 0.8;
      :hover {
        // color: ${({ theme }) => theme.tint_green};
        opacity: 1;
      }
    }
  }
`

