import projectConfig from "projectConfig"
import React from "react"
import { Link } from "gatsby"
import Row from "globalAtoms/Row"
import ElementList from "globalOrganisms/ElementList"

export default function MainContentLayerDefault(props) {
  function getShopProductCategoriesList(shopProductCategories) {
    let content = []

    Object.keys(shopProductCategories).map(element => {
      content.push(
        <Row
          key={shopProductCategories[element]["link"]}
          className={"element-medium"}
        >
          <Link
            to={`/shop/category/${shopProductCategories[element]["link"]}`}
            className={"left-part px-3"}
          >
            {shopProductCategories[element]["title"]}
          </Link>
        </Row>
      )
    })
    return content
  }

  return (
    <>
      <ElementList title={"Каталог"} listClassName={"border-y-inset"}>
        {getShopProductCategoriesList(projectConfig.shopProductCategories)}
      </ElementList>
      <ElementList
        title={"Меню"}
        listClassName={"border-y-inset"}
        sectionClassName={"d-none d-xxl-block"}
      >
        <Row className={"element-medium"}>
          <Link to="/contacts" className={"left-part px-3"}>
            Контакты
          </Link>
        </Row>
        <Row className={"element-medium"}>
          <Link to="/rules" className={"left-part px-3"}>
            Правила
          </Link>
        </Row>
        <Row className={"element-medium"}>
          <Link to="/faq" className={"left-part px-3"}>
            Ответы на частые вопросы
          </Link>
        </Row>
      </ElementList>
    </>
  )
}
